	display: flex;
	font-size: 24px;
	align-items: center;
	color: var(--fontColor);
}

.navContainer {
	align-self: center;
}

.hamburger {
	display: none;
	cursor: pointer;
	margin-bottom: 10px;
}
.bar {
	display: block;
	width: 25px;
	height: 3px;
	margin: 7px auto;
	transition: all 0.3s ease-in-out;
	background-color: var(--boldFontColor);
}
@media screen and (max-width:1510px) {
	.headerContainer {
		padding: 20px 40px;
	}
}

@media screen and (max-width: 1350px) {
	.cartTotal{
		display: none;
	}
	.navContainer {
		padding-top: 0px;
	}
	.hamburger {
		display: block;
		right: 5%;
	}

	.hamburger.active .bar:nth-child(2) {
		opacity: 0;
	}

	.hamburger.active .bar:nth-child(1) {
		-webkit-transform: translateY(10px) rotate(45deg);
		        transform: translateY(10px) rotate(45deg);
	}

	.hamburger.active .bar:nth-child(3) {
		-webkit-transform: translateY(-10px) rotate(-45deg);
		        transform: translateY(-10px) rotate(-45deg);
	}

	.navList {
		display: none;
	}

	.navbarDropdown {
		display: flex;
		position: fixed;
		z-index: 3;
		right: 5%;
		margin-top: 15px;
		flex-direction: column;
		text-align: center;
	}
	.navbarDropdown li {
		font-size: 16px;
		padding: 15px 20px;
		background-color: var(--headerColor);
		border: solid 1px black;
	}
}

@media screen and (max-width: 415px) {
	/* .headerContainer {
		padding: 20px 40px;
		} */
	.logoLink {
		font-size: 25px;
	}
	.bar {
		margin: 5px auto;
	}
	.hamburger.active .bar:nth-child(1) {
		-webkit-transform: translateY(5px) rotate(45deg);
		        transform: translateY(5px) rotate(45deg);
	}

	.hamburger.active .bar:nth-child(3) {
		-webkit-transform: translateY(-10px) rotate(-45deg);
		        transform: translateY(-10px) rotate(-45deg);
	}
}

